<template>
  <div class="images-to-pdf">
    <el-card class="box-card">
      <template #header>
        <div class="card-header">
          <span>Images to PDF</span>
          <el-icon><Document /></el-icon>
        </div>
      </template>

      <el-form :model="form" label-width="120px">
        <el-form-item label="Select Images" required>
          <el-upload
            ref="uploadRef"
            :auto-upload="false"
            :multiple="true"
            :on-change="handleFileChange"
            :before-upload="beforeUpload"
            accept=".jpg,.jpeg,.png,.bmp,.tiff"
            drag
          >
            <el-icon class="el-icon--upload"><upload-filled /></el-icon>
            <div class="el-upload__text">
              Drag images here, or <em>click to upload</em>
            </div>
            <template #tip>
              <div class="el-upload__tip">Supports .jpg/.jpeg/.png/.bmp/.tiff, max 100MB per file</div>
            </template>
          </el-upload>
        </el-form-item>

        <el-form-item label="Fit Mode">
          <el-select v-model="form.fitMode">
            <el-option label="Proportional Fit (contain)" value="contain" />
            <el-option label="Proportional Fill (cover)" value="cover" />
            <el-option label="Stretch Fill (stretch)" value="stretch" />
            <el-option label="Original Center (center)" value="center" />
          </el-select>
        </el-form-item>

        <el-form-item label="Page Size">
          <el-select v-model="form.pageSize">
            <el-option label="A4" value="A4" />
            <el-option label="Letter" value="Letter" />
            <el-option label="Legal" value="Legal" />
          </el-select>
        </el-form-item>

        <el-form-item label="Margins">
          <el-input v-model="form.margins" placeholder="left,top,right,bottom (pt)" />
          <div class="form-tip">Default 36,36,36,36 (unit: pt)</div>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" :loading="loading" :disabled="files.length === 0" @click="handleSubmit">
            {{ loading ? 'Processing...' : 'Start Convert' }}
          </el-button>
          <el-button @click="resetForm">Reset</el-button>
        </el-form-item>
      </el-form>

      <div v-if="result" class="result-section">
        <el-divider>Processing Results</el-divider>
        <el-alert :title="result.msg" type="success" :closable="false" show-icon />
        <div class="result-info">
          <p><strong>Task ID:</strong> {{ result.data.taskId }}</p>
          <p><strong>Output File:</strong> {{ result.data.convertedFile }}</p>
          <el-button type="success" @click="downloadResult">
            <el-icon><Download /></el-icon>
            Download PDF
          </el-button>
        </div>
      </div>
    </el-card>

    <section class="info-block">
      <h2>Feature Description</h2>
      <p>Combine multiple images (JPG/PNG etc.) into a single PDF in order, with page size, orientation, and margins configurable.</p>
      <h3>Use Cases</h3>
      <ul>
        <li>Bundle scanned receipts into a PDF for submission.</li>
        <li>Package product images as a PDF brochure.</li>
        <li>Merge study note photos into a PDF for backup.</li>
      </ul>
    </section>
  </div>
</template>

<script setup>
import { ref, reactive } from 'vue'
import { ElMessage } from 'element-plus'
import { Document, UploadFilled, Download } from '@element-plus/icons-vue'
import { pdfApi } from '@/api'
import { downloadFile } from '@/utils/download'

const uploadRef = ref()
const loading = ref(false)
const files = ref([])
const result = ref(null)

const form = reactive({
  fitMode: 'contain',
  pageSize: 'A4',
  margins: '36,36,36,36'
})

const handleFileChange = (file, fileList) => {
  files.value = fileList.map(f => f.raw).filter(Boolean)
}

const beforeUpload = (file) => {
  const isImage = /\.(jpg|jpeg|png|bmp|tiff)$/i.test(file.name)
  if (!isImage) {
    ElMessage.error('Only image files are supported')
    return false
  }
  return false
}

const handleSubmit = async () => {
  if (files.value.length === 0) {
    ElMessage.warning('Please select images first')
    return
  }
  loading.value = true
  try {
    const formData = new FormData()
    files.value.forEach(f => formData.append('files', f))
    formData.append('fitMode', form.fitMode)
    formData.append('pageSize', form.pageSize)
    formData.append('margins', form.margins)

    const response = await pdfApi.fromImages(formData)
    result.value = response
    ElMessage.success('Conversion successful')
  } catch (e) {
    console.error(e)
  } finally {
    loading.value = false
  }
}

const downloadResult = () => {
  if (result.value?.data?.downloadUrl) {
    try {
      const filename = result.value.data.convertedFile || 'images_merged.pdf'
      
      // 使用统一的下载工具函数
      downloadFile(result.value.data.downloadUrl, filename)
      
      ElMessage.success('Download started')
    } catch (error) {
      console.error('Download failed:', error)
      ElMessage.error('Download failed, please try again')
    }
  } else {
    ElMessage.warning('No file available for download')
  }
}

const resetForm = () => {
  form.fitMode = 'contain'
  form.pageSize = 'A4'
  form.margins = '36,36,36,36'
  files.value = []
  result.value = null
  uploadRef.value?.clearFiles()
}
</script>

<style scoped>
.images-to-pdf { max-width: 800px; margin: 0 auto; }
.card-header { display: flex; justify-content: space-between; align-items: center; font-size: 18px; font-weight: bold; }
.form-tip { font-size: 12px; color: #909399; margin-top: 5px; }
.result-section { margin-top: 20px; }
.result-info { margin-top: 15px; padding: 15px; background-color: #f5f7fa; border-radius: 4px; }

.info-block { margin: 16px auto 0; padding: 16px; border: 1px solid var(--el-card-border-color, #ebeef5); background-color: var(--el-fill-color-blank, #fff); border-radius: var(--el-border-radius-base, 4px); box-sizing: border-box; max-width: 800px; }
</style>
